import '../App.css';
import React, { useRef, useState } from 'react';
import typeData from '../data/types.json';
import Embed from './Embed';
import CheckBox from './CheckBox';
import genreData from '../data/genres.json';
import { useTheme } from '../helpers/theme';

function SubtaskTitle({ title, artist, completed, hasLyrics, hasBackingTrack, difficulty }) {
    const { colorText, filterIcon } = useTheme();

    let titleText = artist ?
        `${artist} - ${title}` :
        title;
    
    const hasDifficulty = !!(difficulty && difficulty >= 1 && difficulty < 5);

    return (
        <div style={{ display: 'flex', flexDirection: 'row', alignItems: 'center', justifyContent: 'center', gap: '8px' }}>
            <h2 style={{ textDecoration: completed ? 'line-through' : 'none', color: colorText }}>
                {titleText}
            </h2>
            {hasDifficulty && (
                <img
                    src={`${process.env.PUBLIC_URL}/icon/stars_${Math.floor(difficulty)}.png`}
                    alt=""
                    style={{
                        width: '24px',
                        height: '24px',
                        filter: filterIcon,
                    }}
                />
            )}
        </div>
    );
    
}

function LessonSubtask({ lesson, subtask, onClickTitle, toggleCompletion, isOpen, completed }) {
    const mainRef = useRef(null);
    const { colorBackgroundLight, colorText } = useTheme();

    return (
        <div
            key={subtask.id}
            style={{
                marginBottom: "8px",
            }}
            ref={mainRef}
        >
            <button
                style={{
                    width: "100%",
                    textAlign: "left",
                    border: "solid",
                    backgroundColor: colorBackgroundLight,
                    borderRadius: isOpen ? "8px 8px 0 0" : "8px",
                    cursor: "pointer",
                    color: colorText,
                }}
                onClick={() => {
                    // if (!isOpen) {
                    //     mainRef.current.scrollIntoView({ behavior: 'smooth', block: 'center' });
                    // }
                    onClickTitle();
                }}
            >
                <div style={{ display: 'flex', flexDirection: 'row', alignItems: 'center', padding: '8px', justifyContent: 'space-between' }}>
                    <SubtaskTitle
                        title={subtask.title}
                        artist={subtask.artist}
                        completed={completed}
                        hasLyrics={subtask.has_vocals}
                        difficulty={subtask.difficulty}
                        hasBackingTrack={subtask.has_backing_track}
                    />
                </div>
            </button>
            {isOpen && (
                <div
                    style={{
                        padding: "12px",
                        backgroundColor: colorBackgroundLight,
                        borderRadius: "0 0 8px 8px",
                    }}
                >
                    <p>{subtask.description}</p>
                    <Embed url={subtask.video_url}/>
                    <CheckBox
                        text="Mark as completed:"
                        onChange={toggleCompletion}
                        checked={completed}
                        textColor={colorText}
                    />
                </div>
            )}
        </div>
    );
}


export default LessonSubtask;
